<template>
	<div class="content">
    <el-tabs v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="" name="">{{fileJson.keyword}}-</el-tab-pane>
      <el-tab-pane label="" name="n">{{fileJson.keyword}}-</el-tab-pane>
      <el-tab-pane label="" name="y">{{fileJson.keyword}}-</el-tab-pane>
    </el-tabs>
		<div class="content-body">
			<vSearch :filename="fileJson.filename" :form="fileJson.searchForm.form" :resetName="['isDraft']"></vSearch>
      <router-link append :to="{ path: 'micropage', query: {
        pageType: fileJson.searchForm.form.pageType,
        filename: fileJson.filename
      }}">
		     <el-button type="primary" style="margin-bottom:16px;" v-text="''+fileJson.keyword"></el-button>
      </router-link>
	    <el-button type="primary" style="margin-bottom:16px;" v-text="''" @click="confirm({
        filename:fileJson.filename,
        method:'cancel',
        form:{
          businessNo:urlParams.businessNo
        },
        title:'',
        msg:'?',
        success:'',
        error:'',
      })"></el-button>
			<el-table v-loading="fileJson.table.loading" :data="fileJson.table.data" border :row-class-name="addRowClass">
				<el-table-column prop="name" label="" min-width="80">
				</el-table-column>
				<el-table-column prop="introduction" label="" min-width="160">
				</el-table-column>
				<el-table-column prop="productCount" label="" min-width="55" align="right">
				</el-table-column>
				<el-table-column prop="isMain" label="" min-width="55">
          <template scope="scope">
            <el-tag :type="scope.row.isMain === 'y' ? 'success' : 'danger'" v-text="scope.row.isMain === 'y' ? '' : ''"></el-tag>
          </template>
				</el-table-column>
				<el-table-column label="" min-width="55">
          <template scope="scope">
            <el-tag :type="scope.row.isDraft === 'y' ? 'primary' : 'danger'" v-text="scope.row.isDraft === 'y' ? '' : ''"></el-tag>
          </template>
				</el-table-column>
				<el-table-column label="" min-width="44">
          <template scope="scope">
            <el-popover :disabled="!scope.row.qrCode || scope.row.isDraft === 'y'" trigger="hover" placement="left">
              <img :src="scope.row.qrCode" style="margin:auto" />
              <div style="text-align:center;">
                <el-button size="small" v-copyUrl="{ showMsg: showMsg }"><input type="text" style="opacity:0;position:fixed;top:-100px;left:-100px;" :value="scope.row.pageUrl"/></el-button>
                <el-button size="small" @click="openWindow(scope.row.pageUrl)"></el-button>
              </div>
              <div slot="reference">
                <el-tag :type="scope.row.qrCode && scope.row.isDraft !== 'y' ? '' : 'warning'" v-text="scope.row.qrCode && scope.row.isDraft !== 'y' ? '' : ''"></el-tag>
              </div>
            </el-popover>
          </template>
				</el-table-column>
				<el-table-column label="" min-width="160">
					<template scope="scope">
            <router-link append style="margin-right:10px" :to="{ path: 'micropage', query: {
              id: scope.row.id,
              isDraft: scope.row.isDraft,
              pageNo: scope.row.pageNo,
              pageType: fileJson.searchForm.form.pageType,
              filename: fileJson.filename
            }}">
  						<el-button type="text" size="small"></el-button>
            </router-link>
    				<el-button type="text" @click="confirm({
              filename:fileJson.filename,
              method:'delete',
              form:{
                pageId:scope.row.id,
                pageNo:scope.row.pageNo
              },
              title:'',
              msg:' [ '+scope.row.name+' ] ?',
              success:'',
              error:'',
            })" size="small"></el-button>
    				<el-button type="text" @click="confirm({
              filename:fileJson.filename,
              method:'copy',
              form:{
                pageId:scope.row.id
              },
              title:'',
              msg:' [ '+scope.row.name+' ] ?',
              success:'',
              error:'',
            })" size="small"></el-button>
    				<el-button v-if="scope.row.isMain !== 'y' && scope.row.isDraft !== 'y'" type="text" @click="confirm({
              filename:fileJson.filename,
              method:'toMain',
              form:{
                pageId:scope.row.id,
                businessNo:urlParams.businessNo,
                pageType:fileJson.searchForm.form.pageType
              },
              title:'',
              msg:' [ '+scope.row.name+' ] ?',
              success:'',
              error:'',
            })" size="small"></el-button>
            <el-button v-if="scope.row.isDraft !== 'y'" type="text" @click="openPreViewPage({
              pageNo: scope.row.pageNo
            })" size="small"></el-button>
            <!-- <el-button type="text" size="small" v-if="scope.row.isMain === 'y'">
              
            </el-button> -->
    				<el-button v-if="scope.row.isDraft === 'y'" type="text" @click="confirm({
              filename:fileJson.filename,
              method:'upgrad',
              form:{
                pageId:scope.row.id,
                businessNo:urlParams.businessNo,
                pageType:fileJson.searchForm.form.pageType
              },
              title:'',
              msg:' [ '+scope.row.name+' ] ?',
              success:'',
              error:'',
            })" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<paginationComponent :fileJson="fileJson"></paginationComponent>
		</div>
    <router-view></router-view>
	</div>
</template>
<script>
import vSearch from './search'
import urlParams from 'assets/urlParams'
import {mapGetters, mapActions} from 'vuex'
export default {
  components: {
    vSearch
  },
  data () {
    return {
      activeName: '',
      urlParams: urlParams
    }
  },
  computed: {
    ...mapGetters([
      'HomePage'// ,vuexstate,fileJson
    ]),
    fileJson () {
      return this.HomePage// ,vuexstate,fileJson
    }
  },
  methods: {
    ...mapActions([
      // 'micropageShow',
      'confirm',
      'searchSetOriginForm',
      'searchBtnClick',
      'openPreViewPage'
    ]),
    tabClick (tab, event) {
      this.activeName !== '0' ? this.fileJson.searchForm.form.isDraft = this.activeName : this.fileJson.searchForm.form.isDraft = ''
      this.searchSetOriginForm({
        filename: this.fileJson.filename
      })
      this.searchBtnClick(this.fileJson.filename)
    },
    addRowClass (row, index) {
      if (row.isMain === 'y') {
        return 'main-row'
      }
    },
    openWindow (url) {
      window.open(url)
    },
    showMsg () {
      this.$message({
        type: 'info',
        message: ''
      })
    }
  },
  directives: {
    copyUrl: {
      bind: function (el, binding, vnode, oldVnode) {
        el.onclick = function () {
          this.querySelector('input').select()
          document.execCommand('Copy')
          binding.value.showMsg()
        }
      }
    }
  }
}
</script>
<style>
.el-table .main-row {
  background: #c9e5f5;
}
</style>



